// export default Login;

"use client";

import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Github } from "lucide-react";
import Link from "next/link";

export default function Login() {
  return (
    <div className="min-h-screen w-full flex flex-col-reverse md:flex-row">
      {/* Left testimonial section */}
      <div className="relative w-full md:w-1/2 bg-black text-white p-8 flex items-end">
        <div className="max-w-md space-y-4 mb-8">
          <blockquote className="text-2xl font-medium leading-tight">
            "This library has saved me countless hours of work and helped me
            deliver stunning designs to my clients faster than ever before."
          </blockquote>
          <cite className="block text-sm not-italic">Sofia Davis</cite>
        </div>
      </div>

      {/* Right form section */}
      <div className="relative w-full md:w-1/2 p-4 md:p-8">
        <div className="absolute top-4 right-4">
          <Button variant="ghost" asChild>
            <Link href="/login">Login</Link>
          </Button>
        </div>

        <div className="max-w-md mx-auto space-y-8 pt-12">
          {/* Logo */}
          <div className="flex items-center gap-2">
            <div className="size-8 rounded bg-black flex items-center justify-center">
              <svg
                className="size-4 text-white"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <path d="M4.5 16.5c-1.5 1.26-2 5-2 5s3.74-.5 5-2c.71-.84.7-2.13-.09-2.91a2.18 2.18 0 0 0-2.91-.09z" />
                <path d="m12 15-3-3a22 22 0 0 1 2-3.95A12.88 12.88 0 0 1 22 2c0 2.72-.78 7.5-6 11a22.35 22.35 0 0 1-4 2z" />
              </svg>
            </div>
            <span className="text-xl font-semibold">Acme Inc</span>
          </div>

          {/* Form */}
          <Card className="border-none shadow-none">
            <CardHeader className="space-y-1 p-0">
              <h1 className="text-2xl font-semibold">Create an account</h1>
              <p className="text-sm text-muted-foreground">
                Enter your email below to create your account
              </p>
            </CardHeader>
            <CardContent className="p-0 space-y-4 pt-4">
              <div className="space-y-2">
                <Input
                  type="email"
                  placeholder="name@example.com"
                  className="h-11"
                />
                <Button className="w-full h-11">Sign in with Email</Button>
              </div>

              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t"></div>
                </div>
                <div className="relative flex justify-center text-xs uppercase">
                  <span className="bg-background px-2 text-muted-foreground">
                    Or continue with
                  </span>
                </div>
              </div>

              <Button variant="outline" className="w-full h-11" type="button">
                <Github className="mr-2 size-4" />
                GitHub
              </Button>
            </CardContent>
            <CardFooter className="p-0 pt-4">
              <p className="text-xs text-center text-muted-foreground w-full">
                By clicking continue, you agree to our{" "}
                <Link href="#" className="underline hover:text-primary">
                  Terms of Service
                </Link>{" "}
                and{" "}
                <Link href="#" className="underline hover:text-primary">
                  Privacy Policy
                </Link>
                .
              </p>
            </CardFooter>
          </Card>
        </div>
      </div>
    </div>
  );
}
